body {
  background-color: #f1f1f1;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.App {
  text-align: center;
  min-width: 728px;
  margin: 0 auto;
  border-radius: 5px;
  box-shadow: 0 0 10px;
}

.App header {
  background-color: rgb(202, 202, 202);
  height: 10vh;
  min-height: 50px;
  color: white;
  width: 100%;
  max-width: 728px;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 99;
  padding: 10px;
  box-sizing: border-box;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  box-shadow: 0 0 15px;
}

.App section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-height: 80vh;
  background-color: #fff;
}

main {
  padding: 10px;
  height: 100vh;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
}

main::-webkit-scrollbar {
  width: 0.4rem;
}

main::-webkit-scrollbar-track {
  background-color: rgb(202, 202, 202);
}

main::-webkit-scrollbar-thumb {
  background-color: rgb(126, 126, 126);
}

form {
  height: 10vh;
  bottom: 0;
  background-color: rgb(24, 23, 23);
  width: 100%;
  max-width: 728px;
  display: flex;
  font-size: 1.5rem;
}

form button {
  width: 20%;
  background-color: #0b93f6;
}

input {
  line-height: 1.5;
  width: 100%;
  font-size: 1.5rem;
  background-color: rgb(202, 202, 202);
  color: rgb(83, 83, 83);
  outline: none;
  border: none;
  padding: 0 10px;
}

button {
  background-color: #282c34; /* Green */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  font-size: 1.25rem;
}

.sign-out {
  border-radius: 5px;
}

button:disabled {
  opacity: 0.8;
  cursor: not-allowed;
}

.sign-in {
  color: #282c34;
  background: white;
  max-width: 400px;
  margin: 0 auto;
}

ul,
li {
  text-align: left;
  list-style: none;
}

p {
  max-width: 500px;
  margin-bottom: 12px;
  line-height: 24px;
  padding: 10px 20px;
  border-radius: 25px;
  position: relative;
  color: white;
  text-align: center;
}

.message {
  display: flex;
  align-items: center;
}

.sent {
  flex-direction: row-reverse;
}

.sent p {
  color: white;
  background-color: #0b93f6;
  align-self: flex-end;
}
.received p {
  background: #e5e5ea;
  color: black;
}

img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin: 2px 5px;
}
